import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthserviceService } from 'src/app/shared/authservice.service';

@Component({
  selector: 'app-information-user',
  templateUrl: './information-user.component.html',
  styleUrls: ['./information-user.component.scss']
})
export class InformationUserComponent implements OnInit {

  login = '';
  constructor(private _authService: AuthserviceService, private _router: Router) {
    this._authService.getLogin()
    .subscribe(
      data => this.login = data.toString(),
      error => this._router.navigate(['/main/login'])
    )
   }

  ngOnInit(): void {
  }

}
