import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { InformationUserComponent } from './Component/information-user/information-user.component';
import { LoginUserComponent } from './Component/login-user/login-user.component';
import { RegisterComponent } from './Component/register/register.component';
import { UserComponent } from './Component/user/user.component';
const routes: Routes = [
    {path: '', component: RegisterComponent},
    {path: 'user', component: UserComponent},
    {path: 'register', component: RegisterComponent},
    {path: 'loginUser', component: LoginUserComponent},
    {path: 'informationUser', component: InformationUserComponent}


];

@NgModule({
  imports: [
  RouterModule.forRoot(routes),
],
  exports: [
    RouterModule,
  ]
})
export class AppRoutingModule { }
